import React from "react";
import { Container, Row, Col, Alert } from "react-bootstrap"
import "../components/Bingo.css"

function Bingo() {
    return (
        <div className="bingoBody py-5">
            <Container>
                <Row>
                    <Col md={4} className="my-4 my-md-0">
                        <div className="bingoCardHeader">
                            <h3 className="py-2">Player numbers</h3>
                        </div>
                        <div className="bingoCard">
                            {playerNumbers.map((playerNumbers) => (
                                <div className="bingoNumber" key={playerNumbers + " playerNum"} data-number={playerNumbers}>
                                    <p>{playerNumbers}</p>
                                </div>
                            ))}
                        </div>
                    </Col>
                    <Col md={4} className={"d-flex justify-content-center align-items-center"}>
                        <div className="centralBall d-flex justify-content-center align-items-center">
                            <div className="bingoCentralNumber" onClick={() => pickAnumber()}>
                                Saca un número
                            </div>
                        </div>
                    </Col>
                    <Col md={4} className="my-4 my-md-0">
                        <div className="bingoCardHeader">
                            <h3 className="py-2">CPU numbers</h3>
                        </div>
                        <div className="bingoCard">
                            {cpuNumbers.map((cpuNumbers) => (
                                <div className="bingoNumber" key={cpuNumbers + " cpuNum"} data-number={cpuNumbers}>
                                    <p>{cpuNumbers}</p>
                                </div>
                            ))}
                        </div>
                    </Col>
                </Row>
                <Row className="mt-5">
                    <Col md={12}>
                        <div className="givenNumbers"></div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

//Defino el pool de numeros(bombo)

let bombo = [];

for (let i = 1; i <= 90; i++) {
    bombo.push(i)
}

//Asigno los numeros del jugador

let playerNumbers = [];

for (let i = 0; i < 15; i++) {
    var numerPlayerCheck = bombo[Math.floor(Math.random() * 90)];
    while (playerNumbers.includes(numerPlayerCheck)) {
        numerPlayerCheck = bombo[Math.floor(Math.random() * 90)];
    }
    playerNumbers.push(numerPlayerCheck)
}

//Asigno los numeros de la cpu

let cpuNumbers = [];

for (let i = 0; i < 15; i++) {
    var numberCpuCheck = bombo[Math.floor(Math.random() * 90)];
    while (cpuNumbers.includes(numberCpuCheck)) {
        numberCpuCheck = bombo[Math.floor(Math.random() * 90)];
    }
    cpuNumbers.push(numberCpuCheck)
}

// Targeteo la bola central
let centralBALL = document.getElementsByClassName("bingoCentralNumber");

//Targeteo la zona de numeros ya selectos
let zoneNumbersSelected = document.getElementsByClassName("givenNumbers");

function pickAnumber() {
    if (bombo.length > 0) {
        let pickedNumber = bombo[Math.floor(Math.random() * bombo.length)];
        centralBALL[0].innerHTML = pickedNumber;
        zoneNumbersSelected[0].innerHTML += '<div class="givenNumbersOut">' + pickedNumber + '</div>';
        for (let i = 0; i < bombo.length; i++) {
            if (bombo[i] === pickedNumber) {
                bombo.splice(i, 1);
            }
        }

        if (playerNumbers.includes(pickedNumber) || cpuNumbers.includes(pickedNumber)) {
            let NumbersQuantity = document.querySelectorAll(`[data-number="${pickedNumber}"]`)
            console.log(NumbersQuantity.length)
            switch (NumbersQuantity.length) {
                case 2:
                    document.querySelectorAll(`[data-number="${pickedNumber}"]`)[0].classList.add("eliminateNumber")
                    document.querySelectorAll(`[data-number="${pickedNumber}"]`)[1].classList.add("eliminateNumber")
                    console.log("lenght 2")
                    break;
                case 1:
                    document.querySelectorAll(`[data-number="${pickedNumber}"]`)[0].classList.add("eliminateNumber")
                    console.log("lenght 1")
                    break;
                default:
                    console.log("default")
                    break;
            }
            for (let i = 0; i < playerNumbers.length; i++) {
                if (playerNumbers[i] === pickedNumber) {
                    playerNumbers.splice(i, 1);
                }
            }
            for (let i = 0; i < cpuNumbers.length; i++) {
                if (cpuNumbers[i] === pickedNumber) {
                    cpuNumbers.splice(i, 1);
                }
            }
        } else {

        }
        console.log('Player numbers remaning: ' + playerNumbers.length)
        console.log('CPU numbers remaning: ' + cpuNumbers.length)
        if (playerNumbers.length === 0) {
            alert("Player wins!!!")
            bombo = []
        } else if (cpuNumbers.length === 0) {
            alert("CPU wins!!!")
            bombo = []
        }
    } else {
        alert("Ya no quedan mas numeros o ha ganado un jugador")
    }
}

// console.log(bombo[Math.floor(Math.random() * 90)])

export default Bingo