import React from "react";
import { Container, Row, Col } from "react-bootstrap"
import "./ProjectSection.css"
import { projects } from "../data/dataProjects";
import { Link } from 'react-router-dom'

function Projectsection() {
    return (
        <section id="preojectSection" className="py-5">
            <Container>
                <Row>
                    <Col>
                        <h2>Algunos de mis proyectos</h2>
                    </Col>
                </Row>
                <Row>
                    {projects.map((projects) => (
                        <Col md={6} lg={4} xl={3} key={projects.name}>
                            <div className="cardProject my-5">

                                <div className="photoPlaceholder">
                                    <img src={require('../img/' + projects.img)} alt={projects.name + " - image"} />
                                </div>

                                <div className="textSec p-3">
                                    <h4>{projects.name}</h4>
                                    <p>{projects.description}</p>
                                </div>

                                <div className="projectbuttonSec p-3">
                                    <Link className="btnProject" to={projects.link} target="_blank" aria-label="Ver proyecto">Ver más</Link>
                                </div>
                            </div>
                        </Col>
                    ))}
                </Row>
            </Container>
        </section>
    )
}

export default Projectsection